* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr;
  grid-template-columns: 50fr 50fr;
}

.App-header {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  padding: 0 8.33% 0 8.33%;
}

.App-main {
  grid-row: 2 / 3;
  grid-column: 1 / 3;
  padding: 1.5em 8.33% 1.5em 8.33%;
  max-width: 100%;
  width: 100%;
}

.App-searcher {
  background-color: #E7E6E6;
  opacity: 0.98;
  display: grid;
  grid-row-start: 1;
  grid-row: 1 / 3;
  grid-column: 1 / 3;
  z-index: 2;
}

.header {
	text-align: center;
}

.header-logo {
	font-family: 'Mukta Malar', sans-serif;
	font-size: 72px;
	font-weight: 700;
	letter-spacing: 30px;
}

.header-logo a {
	text-decoration: none;
	color: black;
}

.header-search {
	color: black;
	font-size: 40px;
}
.Searcher {
    justify-self: stretch;
    background-color: #E7E6E6;
    opacity: 0.98;
}

.SearcherInsides {
    display: grid;
}

.Searcher-searchbar {
    font-family: 'Roboto', sans-serif;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
    height: 65px;
    width: 46vw;
    font-size: 32px;
    border: 2px solid;
    padding: 0.5em;
}


.Searcher-close {
    /* margin-right: 0.6em !important; */
    justify-self: end;
    align-self: center;
}

.Searcher-close-icon {
    font-size: 38px;
}

.Searcher-results {
    margin: 0 15% 0 15%;
}

.SearchResults {
    display: grid;
    padding: 0 8.33% 0 8.33%;
}

@media only screen and (min-width: 768px) {
    .SearchResults {
        grid-template-columns: repeat(12, 1fr);
    }

    .SearchResults-list:nth-child(1) {
        grid-column: 2 / 5;
    }
    
    .SearchResults-list:nth-child(2) {
        grid-column: 6 / 9;
    }
    
    .SearchResults-list:nth-child(3) {
        grid-column: 10 / 13;
    }  
}

@media (max-width: 768px) {
    .SearchResults {
        grid-auto-rows: 1fr;
    }
}

.SearchResults-list > h1 {
    font-size: 32px;
}



